<template>
    <div class="container--text-center" id="SignIn">
      <div class="header">
        <img class="header__image" src="../../assets/slow_logo.png" alt="SLoW">
        <div class="header__slogan">
          Smart Life on the Web
        </div>
      </div>
      <form class="sign-in-wrapper">
        <input type="text" placeholder="아이디">
        <input type="password" placeholder="비밀번호">
        <router-link :to="{path: '/hub/hub-list'}">
          <input type="submit" value="로그인">
        </router-link>
      </form>
      <div class="sign-in-config">
        <ul class="sign-in-config__list">
          <router-link :to="{path: '/auth/search/uid'}">
            <li class="sign-in-config__list__item">아이디 찾기</li>
          </router-link>
          |
          <router-link :to="{path: '/auth/search/upw'}">
            <li class="sign-in-config__list__item">비밀번호 찾기</li>
          </router-link>
          |
          <router-link :to="{path: '/auth/sign-up'}">
            <li class="sign-in-config__list__item">회원 가입</li>
          </router-link>
          </ul>
      </div>
      <div class="footer">
        Copyright©2017 by TechAnd Inc. All rights Reserved.
      </div>
    </div>
</template>

<script>
  export default {
    name: "SignIn"
  }
</script>

<style src="../../style/common/common.css" scoped></style>
<style src="../../style/common/input.css" scoped></style>
<style src="../../style/spa/Auth/SignIn.css" scoped></style>



