export const policy = `제1장 총 칙

제1조[목 적]

이 약관은 SLoW™ IoT platform 회원을 위해 본 사이트가 제공하는 모든 서비스를 이용하는 인터넷회원(이하 회원)과 본 사이트에서 서비스의 이용조건 및 절차에 관한 사항과 기타 필요한 사항을 규정함을 목적으로 합니다.

제2조[약관의 효력과 변경]

1. 서비스는 본 약관에 규정된 조항을 변경 없이 이용자가 수락하는 것을 조건으로 제공됩니다. "이용약관에 동의 합니다"라는 물음에 회원이 '동의' 버튼을 클릭함은 본 약관에 대하여 이용자가 동의한다는 것을 의미합니다.

2. 본 약관은 이용자에게 온라인을 통해 공시함으로써 효력을 발생합니다. 본 사이트는 사정 변경의 경우와 운영상 중요 사유가 있을 때 약관을 임의로 변경할 수 있으며, 변경된 약관은 서비스 화면에 게시되거나 기타의 방법으로 회원에게 공지됨으로써 효력이 발생됩니다.

3. 회원은 변경된 약관에 동의하지 않을 경우, 서비스 이용을 중단하고 탈퇴할 수 있습니다. 약관이 변경된 이후에도 계속적으로 서비스를 이용하는 경우에는 회원이 약관의 변경 사항에 동의한 것으로 봅니다.

제3조[약관 외 준칙] 이 약관에 명시되지 않은 사항이 국내 관계법령에 규정되어 있을 경우에는 관련 법령에 따릅니다.

제2장 회원가입과 서비스 이용

제4조[회원 가입]

1.회원 가입은 이용자의 가입 신청과 이용자의 약관 내용에 대한 동의로 성립됩니다.

2.회원에 가입하여 서비스를 이용하고자 하는 희망자는 본 사이트가 정한 소정의 양식에 따른 개인 신상정보를 제공해야 합니다. 이용자가 제공한 개인정보는 철저히 보호됩니다.

제5조[가입신청의 승낙]

1.본 사이트는 회원이 모든 사항을 정확히 기재하여 신청할 경우 회원 가입을 승낙합니다.

제6조[회원의 혜택]

본 사이트의 인터넷 회원 가입은 무료이며 가입 즉시 모든 서비스를 이용할 수 있습니다.

제3장 회원가입 해지 및 서비스이용 제한

제7조[회원 가입 해지 및 이용 제한]

1.회원이 회원가입을 해지하고자 할 때는 회원 본인이 직접 회원정보변경 페이지에서 해지 신청을 해야 합니다.

2.가입해지를 요청한 후 재가입시에는 새로운 ID로 가입하여야 합니다.

3. 본 사이트는 회원이 다음 사항에 해당하는 행위를 하였을 경우 사전 통지 없이 이용 계약을 해지하거나 또는 기간을 정하여 서비스 이용을 중지할 수 있습니다.
① 범죄 행위에 관련되는 경우
② 사회적 공익을 저해할 목적으로 서비스 이용을 계획 또는 실행할 경우
③ 타인의 ID및 비밀번호를 도용한 경우
④ 타인의 명예를 손상시키거나 불이익을 주는 경우
⑤ 서비스에 위해를 가하는 등 건전한 이용을 저해하는 경우
⑥ 기타 관련법령이나 본 사이트가 정한 이용조건에 위배되는 경우

제8조[이용 제한 절차]

본 사이트는 이용제한을 하고자 하는 경우에는 그 사유, 일시 및 기간을 정하여 이용자의 전자우편 또는 전화 등의 방법에 의하여 해당 이용자 통지합니다. 다만, 본 사이트가 긴급하게 이용을 정지할 필요가 있다고 인정하는 경우에는 그러하지 아니할 수 있습니다.

제4장 책 임

제9조[본 사이트의 의무]

1.본 사이트는 특별한 사정이 없는 한 회원이 신청한 서비스를 이용할 수 있도록 합니다.

2.본 사이트는 이 약관에서 정한 바에 따라 계속적, 안정적으로 서비스를 제공할 의무가 있으며 이를 위해 본 사이트는 노력해야 하며, 부득이한 이유로 서비스가 중단되면 지체없이 이를 수리 복구합니다. 단, 천재지변, 비상사태, 그 밖의 부득이한 경우에는 그 서비스를 일시 중단하거나 중지할 수 있습니다.

3.본 사이트는 서비스 제공으로 알게 된 회원의 신상정보를 본인의 승낙 없이 제3자에게 누설, 배포하지 않습니다.

제10조[회원의 의무]

1.ID와 비밀번호에 관한 모든 관리의 책임은 회원에게 있습니다.

2.회원은 자신의 ID를 타인에게 양도, 증여할 수 없습니다.

3.자신의 ID가 부정하게 사용된 경우, 회원은 반드시 본 사이트에 그 사실을 전자우편이나 기타 방법을 이용해 통보해야 합니다.

4.회원은 이 약관 및 관계법령에서 규정한 사항을 준수하여야 합니다.

제5장 게시물

제11조[회원의 게시물]

본 사이트는 회원의 게시물이 다음 각 항에 해당되는 경우에는 사전통지 없이 삭제합니다.
① 타인의 프라이버시 및 공표권과 같은 법적권리를 훼손, 악용, 도용, 위협하거나 괴롭히거나 또는 달리 위반하는 행위
② 부적절, 모독적, 명예훼손적, 침해적, 음란, 상스럽거나 또는 불법적인 제목, 이름, 자료 또는 정보를 출판, 우송, 게시, 배포 또는 유포시키는 행위
③ 타인의 컴퓨터를 손상시킬 수 있는 바이러스, 오염된 파일, 또는 기타 유사한 소프트웨어 또는 프로그램을 포함하는 자료를 업로드하는 행위
④ 상업적인 목적으로 상품 또는 서비스를 광고 또는 판매하는 행위
⑤ 자료조사, 컨테스트, 피라미드 체계를 행하거나 행운의 편지를 보내는 행위
⑥ 다른 이용자가 서비스를 사용하거나 즐기는 것을 제한하거나 금지시키는 행위

제12조[게시물의 저작권]

본 사이트에 게시된 게시물은 사용자 권한 정책에 따라 다른 이용자와 공유될 수 있습니다.

제13조[게시물에 대한 본 사이트의 책임]

1. 본 사이트는 게시물을 검열할 의무를 지지 않습니다. 그러나, 본 사이트는 적용되는 법률, 규정, 법적 절차를 위하여 정보를 공개하거나, 또는 본 사이트의 전적인 재량으로 정보 내지 자료의 전부 또는 일부를 편집,삭제할 권리를 보유합니다.

2. 회원은 모든 게시판 서비스가 공공적인 통신으로 사적인 통신이 아니며, 그 결과 이용자의 통신이 귀하가 알지 못하게 다른 사람에 의하여 읽힐 수 있음을 인정합니다. 누구에 관한 것인지 식별할 수 있는 이용자 또는 이용자의 자녀들에 관한 정보를 게시할 때는 항상 주의를 기울여야 합니다.

3. 본 사이트는 이용약관을 위반하거나 제12조에 해당될 때 회원이 게시판의 일부 또는 전부에 접속하는 것을 종료시킬 수 있는 권한을 보유합니다.

4. 게시물에 관한 책임 및 이용자가 게시판 서비스에 참여함으로써 발생하는 사건에 대한 책임은 이용자에게 있습니다.

제14조[정보 제공]

1.본 사이트는 회원에게 필요한 정보를 전자메일이나 서신우편 등의 방법으로 전달할 수 있으며, 회원은 이를 원하지 않을 경우 회원정보변경 메뉴에서 정보수신거부를 할 수 있습니다.

2.본 사이트는 비영리 공익활동을 하고 있는 단체와 기관 및 비영리 공익활동 프로그램에 대한 안내를 본 사이트의 판단에 따라 전자우편을 통해 회원들에게 제공할 수 있습니다.

부 칙
1. 본 약관은 2017년 11월 22일부터 적용됩니다`;
export const privacyPolicy = `SLoW™ IoT platform 서비스(이하 “서비스”라 한다)와 관련하여, 본인은 동의내용을 숙지하였으며, 이에 따라 본인의 개인정보를 귀사((주)테크앤)가 수집 및 이용하는 것에 대해 동의합니다.

  1. 개인정보의 수집∙이용 목적
  - 회원관리 : 서비스 이용에 따른 본인식별, 불량회원의 부정한 이용 및 비인가 사용 방지, 가입의사의 확인, 가입 및 횟수 제한, 분쟁 조정을 위한 기록 보존, 서비스 이용에 따른 민원 사항의 처리, 새로운 정보와 고지사항의 안내
  - 서비스 제공 : SLoW™ API, SLoW™ UI, SLoW™ Library 및 개발에 필요한 인증

  2. 수집하는 개인정보의 항목
  - 개인회원 : 아이디, 비밀번호, E-mail

  3. 개인정보의 보유 및 이용 기간
  - 개인정보는 본 서비스를 제공하는 기간 동안에 보유 및 이용되고, 이용자의 서비스 해지(회원탈퇴)시 해당인의 개인정보가 열람 또는 이용될 수 없도록 파기 처리함. 단, 아래의 경우는 예외로 함.

  (1) 법령에서 따로 정하는 경우에는 해당 기간까지 보유
  (2) 부정이용 방지 및 불법적 사용자에 대한 관련 기관 수사협조를 위해 해지 후 6개월까지 보유하고 해당 기간 종료 후 삭제

  4. 본 동의는 서비스의 본질적 기능 제공을 위한 개인정보 수집/이용에 대한 동의로서, 동의를 하는 경우에만 서비스 이용이 가능합니다.

  5. 법령에 따른 개인정보의 수집/이용, 계약의 이행/편익제공을 위한 개인정보 취급위탁 및 개인정보 취급과 관련된 일반 사항은 서비스의 개인정보 취급방침에 따릅니다.`;



