<template>
  <div id="FloatNav">
    <ul class="float-menu" ref="floatNav">
      <router-link :to="{path: '/hub/hub-list'}">
        <li @click="toggleFloatMenu()" class="float-menu__item">허브 목록</li>
      </router-link>
      <router-link :to="{path: '/account/setting'}">
        <li @click="toggleFloatMenu()" class="float-menu__item">회원 관리</li>
      </router-link>
      <router-link :to="{path: '/module/regist'}">
        <li @click="toggleFloatMenu()" class="float-menu__item">모듈 등록</li>
      </router-link>
      <router-link :to="{path: '/auth/sign-in'}">
        <li  @click="toggleFloatMenu()" class="float-menu__item">로그아웃</li>
      </router-link>
    </ul>
    <div class="float-btn" @click="toggleFloatMenu()">
      <i class="fa fa-bars" aria-hidden="true"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FloatNav',
  methods: {
    toggleFloatMenu() {
      const floatMenu = this.$refs.floatNav;
      if (floatMenu.className == "float-menu") {
        floatMenu.className += " show";
      }
      else {
        floatMenu.className = "float-menu";
      }
    },
    closeFloatMenu() {
      const floatMenu = this.$refs.floatNav;
      floatMenu.className = "float-menu";
    },
  }
}
</script>

<style src="../style/common/common.css" scoped></style>
<style src="../style/components/FloatNav.css" scoped></style>



