<template>
  <div id="Nav">
    <div class="nav">
      <router-link :to="{path: '/hub/hub-list'}">
        <img class="nav__image" src="../assets/slow_logo.png" alt="SLoW">
      </router-link>
      <div class="nav__menu-btn" @click="toggleFloatMenu()">
        <i class="fa fa-bars" aria-hidden="true"></i>
      </div>
    </div>
    <ul class="menu hide" @click="toggleFloatMenu()" ref="menu">
      <router-link :to="{path: '/hub/hub-list'}">
        <li @click="toggleFloatMenu()" class="menu__item">허브 목록</li>
      </router-link>
      <router-link :to="{path: '/account/setting'}">
        <li @click="toggleFloatMenu()" class="menu__item">회원 관리</li>
      </router-link>
      <router-link :to="{path: '/module/regist'}">
        <li @click="toggleFloatMenu()" class="menu__item">모듈 등록</li>
      </router-link>
      <router-link :to="{path: '/auth/sign-in'}">
        <li  @click="toggleFloatMenu()" class="menu__item">로그아웃</li>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Nav',
  methods: {
    toggleFloatMenu() {
      const menu = this.$refs.menu;
      if (menu.className == "menu") {
        menu.className += " hide";
      }
      else {
        menu.className = "menu";
      }
    },
    closeFloatMenu() {
      const floatMenu = this.$refs.floatNav;
      floatMenu.className = "float-menu";
    },
  }
}
</script>

<style src="../style/components/Nav.css" scoped></style>



