import VueRouter from 'vue-router'
import Home from '../spa/Home'
import SignIn from '../spa/Auth/SignIn'
import SignUp from '../spa/Auth/SignUp/SignUp'
import SearchID from '../spa/Auth/Search/SearchID'
import SearchPasswd from '../spa/Auth/Search/SearchPasswd'
import HubList from '../spa/Hub/HubList'
import HubSetting from '../spa/Hub/HubSetting'
import DeviceList from '../spa/Device/DeviceList'
import AccountSetting from '../spa/Account/AcountSetting'
import ModuleRegist from '../spa/Module/ModuleRegist'

const routes = [
  {
    path: '/',
    component: SignIn,
    meta: {
      title: 'SLoW',
    }
  },
  {
    path: '/auth/sign-in',
    component: SignIn,
    meta: {
      title: '로그인 - SLoW',
    }
  },
  {
    path: '/auth/sign-up',
    component: SignUp,
    meta: {
      title: '회원가입 - SLoW',
    }
  },
  {
    path: '/auth/search/uid',
    component: SearchID,
    meta: {
      title: '아이디 찾기 - SLoW',
    }
  },
  {
    path: '/auth/search/upw',
    component: SearchPasswd,
    meta: {
      title: '비밀번호 찾기 - SLoW',
    }
  },
  {
    path: '/hub/hub-list',
    component: HubList,
    meta: {
      title: '허브 리스트 - SLoW',
    }
  },
  {
    path: '/hub/hub-setting',
    component: HubSetting,
    meta: {
      title: '허브 설정 - SLoW',
    }
  },
  {
    path: '/device/device-list',
    component: DeviceList,
    meta: {
      title: '장치 리스트 - SLoW',
    }
  },
  {
    path: '/account/setting',
    component: AccountSetting,
    meta: {
      title: '계정관리 - SLoW',
    }
  },
  {
    path: '/module/regist',
    component: ModuleRegist,
    meta: {
      title: '모듈등록 - SLoW',
    }
  },
]

const router = new VueRouter({
  scrollBehavior() {
    return { x: 0, y: 0 };
  },
  mode: 'history',
  routes
})

router.beforeEach(function (to, from, next) {
  if(to.meta && to.meta.title){
    document.title = to.meta.title;
  }
  return next();
});

export default router



